export default {
   layout:"OnlyNav",
  name: "Characters",
  props: {
    type: String
  },
  data() {
    return {
      value: {
        token: "",
        password: ""
      }
    };
  },
  mounted() {},
  methods: {
    reset() {
      if (this.value.password.length < 5) {
        this.$buefy.snackbar.open({
          message: "Password has to be atleast 5 characters long!",
          duration: 5000
        });
      } else {
        this.value.token = window.location.search.split("?token=")[1];
        const data = {
          action: "resetPwdLong",
          value: this.value
        };
        this.$axios(process.env.baseUrl + "authmanagement", {
          method: "post",
          headers: new Headers({ "content-type": "application/json" }),
          data: data
        })
          .then(function(response) {
            return response.data;
          })
          .then(response => {
            if (response.code !== undefined) {
              this.$buefy.snackbar.open(response.message);
            } else {
              this.$buefy.snackbar.open({
                message: "Password has been reset!",
                duration: 15000
              });
              router.push({ path: `/login` });
            }
          })
          .catch(error => {
            if (error.response) {
              this.$buefy.snackbar.open({
                message: error.response.data.message,
                duration: 5000
              });
            }
          });
      }
    }
  },
  components: {}
};
